.App {
  text-align: center;
  background-color: #f0f0f0; /* Heller Grauton */
  min-height: 100vh; /* Vollbildhöhe */
  padding: 20px; /* Etwas Innenabstand für Inhalte */
}

h1 {
  color: #580; /* Farbe für die Überschrift */
}

button {
  background-color: #4CAF50; /* Grüner Hintergrund für Buttons */
  color: white;
  border: none;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
  border-radius: 8px;
}

input, select, textarea {
  margin: 5px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 14px;
}

table {
  width: 100%;
  margin-top: 20px;
  border-collapse: collapse;
}

table, th, td {
  border: 1px solid #999999;
  padding: 8px;
}

th {
  background-color: #4CAF50;
  color: white;
}

td {
  text-align: center;
}

td {
  vertical-align: top; /* Text in Zellen nach oben ausrichten */
}


tr:nth-child(even) {
  background-color: #f2f2f2;
}


ul {
  list-style-type: none; /* Entfernt die Aufzählungszeichen */
  padding-left: 0; /* Entfernt den standardmäßigen Innenabstand auf der linken Seite */
}

.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-content {
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
  max-width: 600px;
}

.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}

.date-day, .date-year {
  display: block; /* Zeilenumbruch zwischen Tag/Monat und Jahr */
}

td.date-cell {
  white-space: nowrap; /* Verhindert Zeilenumbruch innerhalb von <span>-Blöcken */
  text-align: center;
  vertical-align: top;
}

td {
  text-align: center;
  vertical-align: top;
  white-space: nowrap; /* Verhindert ungewollte Zeilenumbrüche */
}

td {
  word-wrap: break-word; /* Erzwingt den Umbruch innerhalb des Wortes */
  white-space: normal;   /* Erlaubt Zeilenumbruch */
}

td.description-column {
  max-width: 300px; /* Begrenze die maximale Breite der Zelle */
  word-break: break-word; /* Bricht Wörter um, wenn sie zu lang sind */
  overflow-wrap: break-word; /* Alternative, um lange Wörter umzubrechen */
  white-space: normal; /* Erlaubt Zeilenumbrüche */
}

table {
  width: 100%; /* Tabelle nimmt die gesamte verfügbare Breite ein */
  border-collapse: collapse; /* Entfernt Doppelrahmen */
}

th, td {
  padding: 8px; /* Abstand innerhalb der Zellen */
  vertical-align: top; /* Text oben ausrichten */
}

td.participants-column {
  max-width: 250px; /* Maximale Breite für die Teilnehmer-Spalte */
  word-wrap: break-word; /* Erzwingt Zeilenumbruch innerhalb von Wörtern */
  white-space: normal;   /* Erlaubt Zeilenumbruch */
}


